body{
  background-color: #141623;
  margin: 0;
  font-family: sans-serif;
}
main{
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background-image: 
  repeating-linear-gradient(
      to right,
      transparent 0 50px, 
      #fff1 50px 51px
  ),
  repeating-linear-gradient(
      to bottom,
      transparent 0 50px, 
      #fff1 50px 51px
  );
  position: relative;
}
form{
  background-color: #eee;
  width: max-content;
  padding: 5px;
  margin: 10px;
  border-radius: 24px;
  display: flex;
  gap: 10px;
}
form input, button{
  width: 40px;
  height: 40px;
  padding: 0;
  border: none;
  background-color: transparent;
  font-size: large;
  cursor: pointer;
}
form input::-webkit-color-swatch-wrapper{
  padding: 0;
}
form input::-webkit-color-swatch{
  border-radius: 50%;
  border: none;
}
#list textarea{
  all: unset;
  color: #d6d6d6;
  min-height: 200px;
}
#list .note{
  background-color: #333;
  width: max-content;
  border-top: 30px solid #844fff;
  border-radius: 15px;
  box-shadow: 0 20px 50px #0004;
  padding: 10px;
  position: absolute;
  top: 60px;
  left: 50px;
}
#list .note span{
  position: absolute;
  bottom: 100%;
  right: 0;
  height: 30px;
  font-family: cursive;
  font-size: large;
  padding-right: 10px;
  cursor: pointer;
}

.add {
  scale: 2.2;
  color: #575757;
}
::-webkit-scrollbar {
  background: #00000000;
  max-width: 4px;
}
::-webkit-scrollbar-thumb {
  background: white;
  border-radius: 10px;
}